  background-position: center top;
  margin: 0 auto;
  max-width: 1440px;
  min-height: 825px;
  position: relative; }
  @media (max-width: 1023px) {
    .page-header__wrapper {
      background-image: none; } }

.page-header__wrapper-about {
  display: grid;
  grid-template-columns: 45% 55%;
  max-width: 920px;
  margin: 0 auto;
  padding: 0 20px; }
  @media (max-width: 1023px) {
