      display: flex;
      flex-direction: column;
      padding: 0;
      max-width: none; } }

.main-nav {
  margin: 0 auto;
  max-width: 873.5px;
  padding-top: 100px; }
  @media (max-width: 1023px) {
    .main-nav {
      padding-top: 0; } }

@media (max-width: 1023px) {
  .main-nav--opened {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    background-color: #FFFFFF;
    z-index: 1000;
    max-width: none;
    padding-top: 100px; } }

@media (max-width: 1023px) {
  .main-nav--nojs .main-nav__list {
    padding-top: 100px;
    display: block; } }

@media (max-width: 1023px) {
  .main-nav--nojs .main-nav__toggle {
    display: none; } }

.main-nav__list {
  padding: 0 20px 0 20px;
  list-style: none;
  display: flex;
  justify-content: space-between;
  position: relative; }
  @media (max-width: 1023px) {
    .main-nav__list {
      display: none;
      flex-direction: column;
      padding: 0;
      margin: 0; } }
  .main-nav__list::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: -9px;
    width: 100%;
    height: 1px;
    background-color: rgba(255, 255, 255, 0.2); }
    @media (max-width: 1023px) {
      .main-nav__list::before {
        display: none; } }

@media (max-width: 1023px) {
  .main-nav--opened .main-nav__list {
    display: flex; } }

@media (max-width: 1023px) {
  .main-nav__item {
    background-color: #F6F6F6;
    margin-bottom: 30px; }
    .main-nav__item:last-child {
      margin-bottom: 138px; } }

.main-nav__item a {
  color: rgba(255, 255, 255, 0.7);
  font-weight: 300;
  font-size: 16px;
  line-height: 32px;
  letter-spacing: -0.005em; }
  @media (max-width: 1023px) {
    .main-nav__item a {
      display: flex;
      color: #48494D;
      font-weight: normal;
      font-size: 20px;
      line-height: 140%;
      padding: 16px;
      position: relative; }
      .main-nav__item a::after {
        content: "";
        position: absolute;
        top: 17px;
        right: 17px;
        width: 25px;
        height: 25px;
        background-image: url(../../img/arrow-icon.svg);
        background-repeat: no-repeat;
        background-size: 25px 25px; } }

.main-nav__toggle {
  display: none; }
  @media (max-width: 1023px) {
    .main-nav__toggle {
      display: block;
      position: absolute;
      width: 50px;
      height: 50px;
      top: 0;
      right: 0;
      background-color: #0AD9C6;
      border: none;
      cursor: pointer; }
      .main-nav__toggle::after {
        content: "";
        position: absolute;
        top: 11px;
        right: 10px;
        width: 30px;
        height: 30px;
        background-image: url(../../img/menu-burger-icon.svg);
        background-repeat: no-repeat;
        background-size: 30px 30px; } }

@media (max-width: 1023px) {
  .main-nav--opened .main-nav__toggle {
    display: block;
    position: absolute;
    width: 50px;
    height: 50px;
    top: 0;
    right: 0;
    background-color: #FFFFFF;
    border: none;
    cursor: pointer; }
    .main-nav--opened .main-nav__toggle::after {
      content: "";
      position: absolute;
      top: 14px;
      right: 12px;
      width: 25px;
      height: 25px;
      background-image: url(../../img/menu-close-icon.svg);
      background-repeat: no-repeat;
      background-size: 25px 25px; } }

.main-nav--opened.main-nav {
  display: block; }

