@font-face {
  font-family: 'Open Sans';
  src:
    local('Open Sans Light'),
    url('../fonts/opensans-light.woff2') format('woff2'),
    url('../fonts/opensans-light.woff') format('woff');
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

